<template>
	<view class="plan-container">
		<!-- È°∂ÈÉ®Ê†áÈ¢òÊ†è -->
		<view class="header">
			<text class="title">ÊóÖË°åËßÑÂàí</text>
			<text class="subtitle">Âú®Âú∞Âõæ‰∏äËßÑÂàíÊÇ®ÁöÑÂÆåÁæéË°åÁ®ã</text>
		</view>

		<!-- Êó•ÊúüÂàáÊç¢Ê®°Âùó -->
		<view class="date-tabs">
			<scroll-view class="date-scroll" scroll-x="true">
				<view 
					v-for="(date, index) in dates" 
					:key="index"
					class="date-tab"
					:class="{ active: currentDateIndex === index }"
					@click="switchDate(index)"
				>
					<text class="date-text">{{ date.name }}</text>
					<text class="date-desc">{{ date.desc }}</text>
				</view>
			</scroll-view>
		</view>

		<!-- Âú∞ÂõæÂ±ïÁ§∫Ê®°Âùó -->
		<view class="map-section">
			<view class="map-header">
				<text class="section-title">Âú∞ÂõæÂ±ïÁ§∫</text>
				<view class="map-controls">
					<button class="control-btn" @click="locateCurrent">üìç</button>
					<button class="control-btn" @click="zoomIn">+</button>
					<button class="control-btn" @click="zoomOut">-</button>
					<button class="control-btn" @click="clearMarkers">üóëÔ∏è</button>
				</view>
			</view>
			<view class="map-container">
				<map 
					id="planMap"
					:longitude="mapCenter.longitude"
					:latitude="mapCenter.latitude"
					:scale="mapScale"
					:markers="mapMarkers"
					:polyline="routePolyline"
					:show-location="true"
					:enable-zoom="true"
					:enable-scroll="true"
					class="tencent-map"
					@tap="onMapTap"
					@markertap="onMarkerTap"
					@regionchange="onRegionChange"
				></map>
				
				<!-- Âú∞ÂõæÂä†ËΩΩÁä∂ÊÄÅ -->
				<view class="map-loading" v-if="isMapLoading">
					<text class="loading-text">Âú∞ÂõæÂä†ËΩΩ‰∏≠...</text>
				</view>
			</view>
		</view>

		<!-- Âú∞ÁÇπÂàóË°®Ê®°Âùó -->
		<view class="locations-section">
			<view class="section-header">
				<text class="section-title">ÂΩìÊó•Ë°åÁ®ã</text>
				<button class="add-btn" @click="showSearchModal">+ Ê∑ªÂä†Âú∞ÁÇπ</button>
			</view>
			<view class="locations-list">
				<view 
					v-for="(location, index) in currentLocations" 
					:key="index"
					class="location-item"
					@click="focusOnLocation(location)"
				>
					<view class="location-info">
						<text class="location-name">{{ location.name }}</text>
						<text class="location-details">{{ location.transport }} ¬∑ {{ location.distance }} ¬∑ {{ location.duration }}</text>
					</view>
					<view class="location-actions">
						<button class="action-btn edit-btn" @click.stop="editLocation(location)">ÁºñËæë</button>
						<button class="action-btn delete-btn" @click.stop="deleteLocation(index)">Âà†Èô§</button>
					</view>
				</view>
			</view>
		</view>

		<!-- Ë∑ØÁ∫øËßÑÂàíÊ®°Âùó -->
		<view class="route-section">
			<view class="section-header">
				<text class="section-title">Ë∑ØÁ∫øËßÑÂàí</text>
				<button class="route-btn" @click="generateRoute">Êô∫ËÉΩË∑ØÁ∫øËßÑÂàí</button>
			</view>
			<view class="route-info">
				<text class="route-total">ÊÄªË°åÁ®ãÔºö{{ routeInfo.totalDistance }} ¬∑ {{ routeInfo.totalDuration }}</text>
				<view class="route-segments">
					<view 
						v-for="(segment, index) in routeInfo.segments" 
						:key="index"
						class="route-segment"
					>
						<text class="segment-info">{{ segment.distance }} {{ segment.duration }}</text>
					</view>
				</view>
			</view>
		</view>

		<!-- ÊêúÁ¥¢Ê®°ÊÄÅÊ°Ü -->
		<view class="modal" v-if="showSearch">
			<view class="modal-content">
				<view class="modal-header">
					<text class="modal-title">ÊêúÁ¥¢Âú∞ÁÇπ</text>
					<button class="close-btn" @click="hideSearchModal">√ó</button>
				</view>
				<view class="search-input">
					<input 
						v-model="searchKeyword" 
						placeholder="ËæìÂÖ•ÊôØÁÇπ„ÄÅ‰ΩèÂÆø„ÄÅ‰∫§ÈÄöÊû¢Á∫ΩÁ≠âÂÖ≥ÈîÆËØç"
						class="search-field"
					/>
					<button class="search-btn" @click="searchLocations">ÊêúÁ¥¢</button>
				</view>
				<view class="search-results">
					<view 
						v-for="(result, index) in searchResults" 
						:key="index"
						class="search-result"
						@click="addLocationFromSearch(result)"
					>
						<text class="result-name">{{ result.name }}</text>
						<text class="result-type">{{ result.type }}</text>
					</view>
				</view>
			</view>
		</view>

		<!-- Â§áÊ≥®ÁºñËæëÊ®°ÊÄÅÊ°Ü -->
		<view class="modal" v-if="showNoteModal">
			<view class="modal-content">
				<view class="modal-header">
					<text class="modal-title">Ê∑ªÂä†Â§áÊ≥®</text>
					<button class="close-btn" @click="hideNoteModal">√ó</button>
				</view>
				<view class="note-input">
					<textarea 
						v-model="currentNote" 
						placeholder="ËæìÂÖ•ÂØπËØ•Âú∞ÁÇπÁöÑË°åÁ®ãÂÆâÊéí„ÄÅÁâπËâ≤ËØ¥ÊòéÁ≠âÂ§áÊ≥®‰ø°ÊÅØ"
						class="note-field"
						maxlength="200"
					></textarea>
					<text class="note-count">{{ currentNote.length }}/200</text>
				</view>
				<view class="modal-actions">
					<button class="cancel-btn" @click="hideNoteModal">ÂèñÊ∂à</button>
					<button class="confirm-btn" @click="saveNote">‰øùÂ≠ò</button>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import { mapConfig, mapUtils } from '@/config/map.config.js'
	
	export default {
		data() {
			return {
				currentDateIndex: 0,
				dates: [
					{ name: '7Êúà3Âè∑', desc: 'Á¨¨‰∏ÄÂ§©' },
					{ name: '7Êúà4Âè∑', desc: 'Á¨¨‰∫åÂ§©' },
					{ name: '7Êúà5Âè∑', desc: 'Á¨¨‰∏âÂ§©' },
					{ name: '7Êúà6Âè∑', desc: 'Á¨¨ÂõõÂ§©' }
				],
				locations: {
					0: [
						{ 
							name: 'Âåó‰∫¨ÊïÖÂÆ´', 
							type: 'ÊôØÁÇπ',
							transport: 'È©æËΩ¶',
							distance: '7km',
							duration: '21ÂàÜÈíü',
							note: 'Âª∫ËÆÆ‰∏äÂçà9ÁÇπÂà∞ËææÔºåÊ∏∏ËßàÊó∂Èó¥Á∫¶3Â∞èÊó∂',
							longitude: 116.3974,
							latitude: 39.9093
						},
						{ 
							name: 'ÁéãÂ∫ú‰∫ïÂ§ßË°ó', 
							type: 'Ë¥≠Áâ©',
							transport: 'Ê≠•Ë°å',
							distance: '3km',
							duration: '11ÂàÜÈíü',
							note: 'ÂçàÈ§êÊé®ËçêÔºöÂÖ®ËÅöÂæ∑ÁÉ§È∏≠',
							longitude: 116.4179,
							latitude: 39.9089
						}
					],
					1: [],
					2: [],
					3: []
				},
				
				// Âú∞ÂõæÁõ∏ÂÖ≥Êï∞ÊçÆ
				mapCenter: {
					longitude: mapConfig.defaultCenter.longitude,
					latitude: mapConfig.defaultCenter.latitude
				},
				mapScale: mapConfig.zoomLevel,
				mapMarkers: [],
				routePolyline: [],
				isMapLoading: false,
				
				showSearch: false,
				showNoteModal: false,
				searchKeyword: '',
				searchResults: [
					{ name: 'Â§©ÂÆâÈó®ÂπøÂú∫', type: 'ÊôØÁÇπ' },
					{ name: 'È¢êÂíåÂõ≠', type: 'ÊôØÁÇπ' },
					{ name: 'Âåó‰∫¨È¶ñÈÉΩÂõΩÈôÖÊú∫Âú∫', type: '‰∫§ÈÄöÊû¢Á∫Ω' },
					{ name: 'Âåó‰∫¨È•≠Â∫ó', type: '‰ΩèÂÆø' },
					{ name: 'ÂÖ®ËÅöÂæ∑ÁÉ§È∏≠Â∫ó', type: 'È§êÈ•Æ' }
				],
				currentNote: '',
				currentEditingLocation: null,
				routeInfo: {
					totalDistance: '10km',
					totalDuration: '32ÂàÜÈíü',
					segments: [
						{ distance: '7km', duration: '21ÂàÜÈíü' },
						{ distance: '3km', duration: '11ÂàÜÈíü' }
					]
				}
			}
		},
		computed: {
			currentLocations() {
				return this.locations[this.currentDateIndex] || []
			}
		},
		methods: {
			// Êó•ÊúüÂàáÊç¢
			switchDate(index) {
				this.currentDateIndex = index
			},

			// Âú∞ÂõæÊéßÂà∂
			zoomIn() {
				if (this.mapScale < 20) {
					this.mapScale += 1;
				}
			},
			zoomOut() {
				if (this.mapScale > 3) {
					this.mapScale -= 1;
				}
			},
			
			// ÂÆö‰ΩçÂà∞ÂΩìÂâç‰ΩçÁΩÆ
			async locateCurrent() {
				try {
					const location = await mapUtils.getCurrentLocation();
					this.mapCenter.longitude = location.longitude;
					this.mapCenter.latitude = location.latitude;
					this.mapScale = 16;
					
					uni.showToast({
						title: 'Â∑≤ÂÆö‰ΩçÂà∞ÂΩìÂâç‰ΩçÁΩÆ',
						icon: 'success'
					});
				} catch (error) {
					console.error('ÂÆö‰ΩçÂ§±Ë¥•:', error);
					uni.showToast({
						title: 'ÂÆö‰ΩçÂ§±Ë¥•ÔºåËØ∑Ê£ÄÊü•ÊùÉÈôêËÆæÁΩÆ',
						icon: 'none'
					});
				}
			},
			
			// Ê∏ÖÈô§Ê†áËÆ∞
			clearMarkers() {
				if (this.mapMarkers.length === 0) {
					uni.showToast({
						title: 'ÊöÇÊó†Ê†áËÆ∞ÂèØÊ∏ÖÈô§',
						icon: 'none'
					});
					return;
				}
				
				uni.showModal({
					title: 'Á°ÆËÆ§Ê∏ÖÈô§',
					content: 'Á°ÆÂÆöË¶ÅÊ∏ÖÈô§ÊâÄÊúâÂú∞ÂõæÊ†áËÆ∞ÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							this.mapMarkers = [];
							this.routePolyline = [];
							uni.showToast({
								title: 'Ê†áËÆ∞Â∑≤Ê∏ÖÈô§',
								icon: 'success'
							});
						}
					}
				});
			},
			
			// Âú∞ÂõæÁÇπÂáª‰∫ã‰ª∂
			onMapTap(e) {
				const { latitude, longitude } = e.detail;
				
				uni.showModal({
					title: 'Ê∑ªÂä†Âú∞ÁÇπ',
					content: 'ÊòØÂê¶Âú®Ê≠§‰ΩçÁΩÆÊ∑ªÂä†ÊóÖË°åÂú∞ÁÇπÔºü',
					showCancel: true,
					cancelText: 'ÂèñÊ∂à',
					confirmText: 'Ê∑ªÂä†',
					success: async (res) => {
						if (res.confirm) {
							try {
								// Ëé∑ÂèñÂú∞ÂùÄ‰ø°ÊÅØ
								const addressInfo = await mapUtils.reverseGeocode(latitude, longitude);
								
								// Ê∑ªÂä†Âà∞ÂΩìÂâçÊó•ÊúüÁöÑÂú∞ÁÇπÂàóË°®
								const newLocation = {
									name: addressInfo.address || 'Ëá™ÂÆö‰πâÂú∞ÁÇπ',
									type: 'ÊôØÁÇπ',
									transport: 'È©æËΩ¶',
									distance: '0km',
									duration: '0ÂàÜÈíü',
									note: '',
									longitude: longitude,
									latitude: latitude
								};
								
								if (!this.locations[this.currentDateIndex]) {
									this.locations[this.currentDateIndex] = [];
								}
								this.locations[this.currentDateIndex].push(newLocation);
								
								// Ê∑ªÂä†Âú∞ÂõæÊ†áËÆ∞
								this.addMapMarker(newLocation);
								
								uni.showToast({
									title: 'Âú∞ÁÇπÊ∑ªÂä†ÊàêÂäü',
									icon: 'success'
								});
							} catch (error) {
								console.error('Ê∑ªÂä†Âú∞ÁÇπÂ§±Ë¥•:', error);
								uni.showToast({
									title: 'Âú∞ÁÇπÊ∑ªÂä†Â§±Ë¥•',
									icon: 'none'
								});
							}
						}
					}
				});
			},
			
			// Ê†áËÆ∞ÁÇπÁÇπÂáª‰∫ã‰ª∂
			onMarkerTap(e) {
				const markerId = e.markerId;
				const marker = this.mapMarkers.find(m => m.id === markerId);
				if (marker) {
					uni.showModal({
						title: marker.title,
						content: 'ÊÇ®ÊÉ≥ÂØπËøô‰∏™Âú∞ÁÇπÂÅö‰ªÄ‰πàÔºü',
						showCancel: true,
						cancelText: 'ÂèñÊ∂à',
						confirmText: 'Êü•ÁúãËØ¶ÊÉÖ',
						success: (res) => {
							if (res.confirm) {
								// ÂèØ‰ª•Âú®ËøôÈáåÂÆûÁé∞Ë∑≥ËΩ¨Âà∞Âú∞ÁÇπËØ¶ÊÉÖÁöÑÂäüËÉΩ
								uni.showToast({
									title: 'Êü•ÁúãÂú∞ÁÇπËØ¶ÊÉÖ',
									icon: 'none'
								});
							}
						}
					});
				}
			},
			
			// Âú∞ÂõæÂå∫ÂüüÂèòÂåñ‰∫ã‰ª∂
			onRegionChange(e) {
				// ÂèØ‰ª•Âú®ËøôÈáåÂ§ÑÁêÜÂú∞ÂõæÊãñÂä®ÊàñÁº©Êîæ‰∫ã‰ª∂
				if (e.type === 'end') {
					// Âú∞ÂõæÊãñÂä®ÁªìÊùüÔºåÂèØ‰ª•Ëé∑ÂèñÊñ∞ÁöÑ‰∏≠ÂøÉÁÇπÂùêÊ†á
				}
			},
			
			// Ê∑ªÂä†Âú∞ÂõæÊ†áËÆ∞
			addMapMarker(location) {
				const marker = {
					id: Date.now(),
					longitude: location.longitude,
					latitude: location.latitude,
					title: location.name,
					iconPath: '/static/images/map/marker.png',
					width: 30,
					height: 30,
					callout: {
						content: location.name,
						color: '#ffffff',
						bgColor: '#1A9E8F',
						display: 'ALWAYS'
					}
				};
				this.mapMarkers.push(marker);
			},
			
			// Êõ¥Êñ∞Âú∞ÂõæÊ†áËÆ∞
			updateMapMarkers() {
				this.mapMarkers = [];
				const currentLocations = this.locations[this.currentDateIndex] || [];
				currentLocations.forEach(location => {
					this.addMapMarker(location);
				});
			},

			// ÊêúÁ¥¢ÂäüËÉΩ
			showSearchModal() {
				this.showSearch = true
			},
			hideSearchModal() {
				this.showSearch = false
				this.searchKeyword = ''
			},
			searchLocations() {
				if (!this.searchKeyword.trim()) {
					uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÖ≥ÈîÆËØç', icon: 'none' })
					return
				}
				// Ê®°ÊãüÊêúÁ¥¢
				uni.showToast({ title: `ÊêúÁ¥¢: ${this.searchKeyword}`, icon: 'none' })
			},

			// Âú∞ÁÇπÁÆ°ÁêÜ
			addLocationFromSearch(result) {
				const newLocation = {
					name: result.name,
					type: result.type,
					transport: 'È©æËΩ¶',
					distance: '0km',
					duration: '0ÂàÜÈíü',
					note: ''
				}
				this.locations[this.currentDateIndex].push(newLocation)
				this.hideSearchModal()
				uni.showToast({ title: 'Âú∞ÁÇπÊ∑ªÂä†ÊàêÂäü', icon: 'success' })
			},

			editLocation(location) {
				this.currentEditingLocation = location
				this.currentNote = location.note || ''
				this.showNoteModal = true
			},

			deleteLocation(index) {
				uni.showModal({
					title: 'Á°ÆËÆ§Âà†Èô§',
					content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Âú∞ÁÇπÂêóÔºü',
					success: (res) => {
						if (res.confirm) {
							this.locations[this.currentDateIndex].splice(index, 1)
							uni.showToast({ title: 'Âà†Èô§ÊàêÂäü', icon: 'success' })
						}
					}
				})
			},

			// Â§áÊ≥®ÁÆ°ÁêÜ
			hideNoteModal() {
				this.showNoteModal = false
				this.currentNote = ''
				this.currentEditingLocation = null
			},

			saveNote() {
				if (this.currentEditingLocation) {
					this.currentEditingLocation.note = this.currentNote
					uni.showToast({ title: 'Â§áÊ≥®‰øùÂ≠òÊàêÂäü', icon: 'success' })
				}
				this.hideNoteModal()
			},

			// Ë∑ØÁ∫øËßÑÂàí
			generateRoute() {
				if (this.currentLocations.length < 2) {
					uni.showToast({ title: 'ËØ∑Ëá≥Â∞ëÊ∑ªÂä†2‰∏™Âú∞ÁÇπËøõË°åË∑ØÁ∫øËßÑÂàí', icon: 'none' })
					return
				}
				uni.showToast({ title: 'Ê≠£Âú®ÁîüÊàêÊô∫ËÉΩË∑ØÁ∫ø...', icon: 'loading' })
				// Ê®°ÊãüË∑ØÁ∫øËßÑÂàí
				setTimeout(() => {
					this.routeInfo = {
						totalDistance: `${this.currentLocations.length * 5}km`,
						totalDuration: `${this.currentLocations.length * 15}ÂàÜÈíü`,
						segments: this.currentLocations.map((_, index) => ({
							distance: `${index + 1}km`,
							duration: `${index + 5}ÂàÜÈíü`
						}))
					}
					uni.showToast({ title: 'Ë∑ØÁ∫øËßÑÂàíÂÆåÊàê', icon: 'success' })
				}, 1000)
			},

			// Âú∞ÂõæËÅöÁÑ¶
			focusOnLocation(location) {
				uni.showToast({ 
					title: `ËÅöÁÑ¶Âà∞: ${location.name}`,
					icon: 'none' 
				})
			},

			goBack() {
				uni.navigateBack()
			}
		}
	}
</script>

<style>
	.plan-container {
		min-height: 100vh;
		background-color: #f5f7fa;
		padding: 20rpx;
	}
	
	.header {
		text-align: center;
		margin-bottom: 30rpx;
		padding: 20rpx 0;
		background: linear-gradient(135deg, #1A9E8F 0%, #2ECC71 100%);
		border-radius: 20rpx;
		color: white;
	}
	
	.title {
		font-size: 40rpx;
		font-weight: bold;
		display: block;
		margin-bottom: 10rpx;
	}
	
	.subtitle {
		font-size: 26rpx;
		opacity: 0.9;
		display: block;
	}
	
	/* Êó•ÊúüÊ†áÁ≠æÊ†∑Âºè */
	.date-tabs {
		margin-bottom: 30rpx;
	}
	
	.date-scroll {
		white-space: nowrap;
	}
	
	.date-tab {
		display: inline-block;
		padding: 20rpx 30rpx;
		margin-right: 15rpx;
		background-color: white;
		border-radius: 15rpx;
		text-align: center;
		min-width: 150rpx;
		box-shadow: 0 2rpx 10rpx rgba(0,0,0,0.1);
		transition: all 0.3s;
	}
	
	.date-tab.active {
		background: linear-gradient(135deg, #1A9E8F 0%, #2ECC71 100%);
		color: white;
	}
	
	.date-text {
		font-size: 28rpx;
		font-weight: bold;
		display: block;
	}
	
	.date-desc {
		font-size: 22rpx;
		opacity: 0.8;
		display: block;
		margin-top: 5rpx;
	}
	
	/* Âú∞ÂõæÂå∫ÂüüÊ†∑Âºè */
	.map-section {
		background-color: white;
